  html:lang(zh) {
    font-family: Helvetica, Arial, "Microsoft Yahei", "微软雅黑", STXihei, "华文细黑", sans-serif; }
  html:lang(ko) {
    line-height: 1.15; }
  html:lang(en) {
    line-height: 1.15; }
  html:lang(ja) {
    line-height: 1.15; }
  html:lang(zh) {
    line-height: 1.15; }
  html:lang(ko) {
    word-break: keep-all; }
  html:lang(en) {
    word-break: break-all; }
  html:lang(ja) {
    word-break: break-all; }
  html:lang(zh) {
    word-break: break-all; }
  html:lang(ko) {
    word-wrap: break-word; }
  html:lang(en) {
    word-wrap: break-word; }
  html:lang(ja) {
    word-wrap: break-word; }
  html:lang(zh) {
    word-wrap: break-word; }
  html:lang(ko) {
    letter-spacing: 0; }
  html:lang(en) {
    letter-spacing: 0; }
  html:lang(ja) {
    letter-spacing: 0; }
  html:lang(zh) {
    letter-spacing: 0; }

@media (min-width: 1024px) and (max-width: 1279px) {
  html {
    font-size: calc(10px + 12 * ((100vw - 1024px) / 1279)); } }

@media (min-width: 1024px) {
  html {
    font-size: 10px; } }

body, div, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre,
code, form, fieldset, legend, input, textarea, p, blockquote, th, td,
article, aside, canvas, details, figcaption, figure, footer, header,
hgroup, nav, section, time, mark, audio, video {
  margin: 0;
  padding: 0; }

body {
  color: #000000;
  background-color: #ffffff; }

img {
  border: 0;
  border-style: none; }

button {
  border: 0;
  background: none;
  padding: 0;
  cursor: pointer; }

button, input, select, optgroup, textarea {
  margin: 0;
  font-size: 100%;
  font-family: inherit; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

fieldset {
  border: 0; }

legend {
  color: #000; }

address, caption, cite, code, dfn, em, strong, th, var {
  font-style: normal;
  font-weight: normal; }

ol, ul {
  list-style: none; }

caption, th {
  text-align: left; }

h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-weight: normal;
  line-height: 1; }

caption, hr, legend {
