/*! skipnav */
.skipnav {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 9999;
  width: 100%;
  height: 0;
  line-height: 1; }

.skipnav a {
  display: block;
  overflow: hidden;
  width: 1px;
  height: 1px;
  margin: 0 -1px -1px 0;
  padding: 0;
  font-size: 0;
  line-height: 1; }

.skipnav a:active, .skipnav a:focus, .skipnav a:hover {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 50;
  width: 100%;
  height: 19px;
  margin: 0;
  padding: 5px 0;
  border: 1px solid #c4d7ea;
  background: #2b6cb0;
  color: #fff;
  font-weight: 700;
  font-size: 1.333em;
  text-align: center;
  line-height: 100%; }

/*! Browser Upgrade Prompt */
.browserupgrade {
  margin: .2em 0;
  padding: .2em 0;
  background: #ccc;
  color: #000;
  font-size: 1px; }

.browserupgrade strong {
  color: #000;
  font-weight: 700; }

.browserupgrade a {
  color: #8d0080;
  font-weight: 700;
  text-decoration: underline; }

/*! dimm */
.dimm {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 99;
  background: #000;
  opacity: .6; }

.dimm img {
  display: block; }

